<template>
  <v-container
    fluid
    fill-height
  >
    <v-layout
      align-center
      justify-center
    >
      <v-flex
        xs12
        sm8
        md4
      >
        <v-card class="elevation-12">
          <v-toolbar
            color="primary"
            dark
            flat
          >
            <v-toolbar-title>Register</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-card-text>
            <v-form v-model="valid">
              <v-text-field
                label="Username"
                name="username"
                prepend-icon="mdi-account-outline"
                type="text"
                :rules="usernameRules"
                v-model="username"
                required
              ></v-text-field>

              <v-text-field
                id="password"
                label="Password"
                name="password"
                prepend-icon="mdi-lock-outline"
                type="password"
                :rules="passwordRules"
                v-model="password"
                required
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn
              small
              text
              @click.stop="openLogin"
            >Bereits Registriert? Zum Login</v-btn>
            <v-spacer></v-spacer>
            <v-btn color="primary">Register</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'register',
  data: () => ({
    valid: false,
    username: null,
    password: null,
    usernameRules: [
      v => !!v || 'Username notwendig'
    ],
    passwordRules: [
      v => !!v || 'Passwort notwendig',
      v => {
        if (v) {
          return v.length >= 8 || 'Passwort muss l√§nger als 8 Zeichen sein'
        } else {
          return ''
        }
      }
    ]
  }),
  methods: {
    register() {

    },
    openLogin() {
      this.$router.push('/login')
    }
  }
}
</script>
